
import VueRouter from 'vue-router'
import News from './components/News.vue'
import Main from './components/Main.vue'
import ProductCore from './components/ProductCore.vue'
import Introduce from './components/Introduce.vue'
import Intro from './components/intro/Intro'
import Culture from './components/intro/Culture'
import Honer from './components/intro/Honer'
import Develop from './components/intro/Develop'
import Contact from './components/Contact'

const Router = new VueRouter({
    routes:[
        { path: '/', component: Main },
        { path: '/core', component: ProductCore },
        { path: '/news', component: News },
        { path: '/intro', component: Introduce,
            children:[
                { path: '/intro', component: Intro },
                { path: '/culture', component: Culture },
                { path: '/develop', component: Develop },
                { path: '/honer', component: Honer }
            ]
        },
        { path: '/contact', component: Contact },
    ],
    mode: "history"
})

export default Router;
