import Vue from 'vue'
import App from './App.vue'
import iView from 'iview'
import VueRouter from 'vue-router'


import 'iview/dist/styles/iview.css'
import './assets/css/base.css'

import Router from './router.js'


Vue.config.productionTip = false
Vue.use(iView)
Vue.use(VueRouter)

new Vue({
  render: h => h(App),
  router: Router
}).$mount('#app')
